import React, { useState } from 'react';
import Portfolioin from './Portfolioin.js'

function Portfolio(props) {
  const [ worknm, worknmupdate ] = useState(['','reactid']);
  const btnupdate = (e) => {
    const newworknm = e.target.getAttribute('datasrc').split(' , ');

    // console.log(newworknm, typeof newworknm)
    worknmupdate(newworknm);

  }
  return (
    <div>

      <div className='d-flex justify-content-center'>
        <button onClick={ btnupdate } datasrc={['','reactid']}>PWA </button>
        <button onClick={ btnupdate } datasrc={['EC ','ecid']}>EC </button>
        <button onClick={ btnupdate } datasrc={['CMS ','cmsid']}>CMS </button>
        <button onClick={ btnupdate } datasrc={['Design ','figmaid']}>Design </button>
      </div>
      <Portfolioin dbobjkey={worknm[0]} portfolioid={worknm[1]}></Portfolioin>

    </div>
  );
}

export default Portfolio;